.home-page {
   padding-top:6vw;
   display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
 background-color: #f4f4f5;

 @media only screen and (max-width: 600px) {
    padding-top:20vw;
    
        }
    .create-post {
        display:flex;
        align-items: center;
        justify-content: center;
      
        background-color: #ffffff;
        width:50vw;
        height:4vw;
        gap:1vw;
        
        @media only screen and (max-width: 600px) {
            height:10vw;
                width:80vw;
                }
       .post-image {
        border-radius: 20vw;
        height:3vw;
        @media only screen and (max-width: 600px) {
            height:5vw;
            
                }
       }
        input {
            border:none;
            border-radius: 0.5vw;;
            height:2.5vw;
            width:40vw;
            background-color: rgb(247, 247, 247);
            font-size: 1.5vw;
            @media only screen and (max-width: 600px) {
                height:5vw;
                  width: 60vw;
                  font-size: 4vw;
                    }
        }
        input:focus {
           outline: none;
        }
       
        
    }
}